import React from "react";
import { Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import CardContent from "../../utils/constants/Courses"
import SourcesContent from "../content/sources";

const useStyles = makeStyles((theme) => ({
    divider: {
        width: '6%',
        border: '2px solid #00bdff',
        borderRadius: '5px',
    },
    sourcesContainer: {
        width:'100%',
        padding:theme.spacing(5),        
        margin:'0px',
    },
    mainContainer3: {
        background:'#f4f4f9',
        paddingTop:'20px',
        color: '#00287b',
    }
}));

const SourcesContainer = () => {
    const classes = useStyles()
    return (
        <section className={classes.mainContainer3}>
            <Typography  id="Sources" className={classes.sourcesHeading} variant="h4" align='center' style={{ width: '100%' }}>
                {CardContent[2].heading}
            </Typography>
            <hr className={classes.divider}></hr>
            <Typography className={classes.sourcesSubHeading} variant="h6" align='center' style={{ width: '100%' }}>
                {CardContent[2].subHeading}
            </Typography>
            <Grid
                container
                spacing={5}
                justify="center"
                className={classes.sourcesContainer}
            >
                {CardContent[2].content.map((item) => (
                    <Grid key={item.title} item xs={12} sm={6} md={4} lg={4} xl={4}>
                        <SourcesContent
                            mainHeading={item.title}
                            subHeading={item.subTitle}
                            link={item.to}
                            image={item.image}
                        />
                    </Grid>
                ))}
            </Grid>
        </section>
    );
};

export default SourcesContainer;
